<template>
    <div>
        <h1 class="divided">伟业</h1>
        <go-back />
        <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div v-for="(v,k) in result" :key="k">
                <rotate3DCard trigger="hover" direction="row">
                <!-- 正面 -->
                <div slot="cz">
                    {{k}}
                </div>
                <!-- 反面 -->
                <div slot="cf">
                    <div>名称：{{v.name}}</div>
                    <div>达成条件：{{v.subname}}</div>
                    <div>{{v.historyname}}</div>

                    <div>奖励</div>
                    <div class="reward">
                        <div v-for="(vv,kk) in v.mailItem" :key="kk">
                            <item-icon-tooltip
                                :itemId="vv.itemId" 
                                :itemStub="vv.item"
                                :count="vv.count"
                                :noLink="noLink">
                            </item-icon-tooltip> 
                        </div>
                    </div>

                    <div v-if="v.relateItem.length>0">
                        <div>相关物品</div>
                        <div class="reward">
                            <div v-for="(vv,kk) in v.relateItem" :key="kk">
                                <item-icon-tooltip
                                    :itemId="vv.id" 
                                    :itemStub="vv"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip> 
                            </div>
                        </div>
                    </div>
                </div>
                
                </rotate3DCard>
            </div>
        </template>
    </div>
</template>
<script>
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import rotate3DCard from "@/components/util/rotate3DCard"
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
export default {
    components:{
        rotate3DCard,
        GoBack,
        ItemIconTooltip
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
        }
    },
    created(){
        this.fetchData();
    },
    methods:{
        fetchData(){
            if(GameInfo.FeatCache){
                this.result = GameInfo.FeatCache;
                this.loading = false;
            }else{
            GameInfo.Feat(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
.reward {
    display: flex;
}
</style>